@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@1,100;1,300;1,500;1,900&display=swap');

body {
  font-family: 'Roboto', sans-serif;;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}
/* animation */

@keyframes anvil {
  0% {
    transform: scale(1) translateY(0px);
    opacity: 0;
    box-shadow: 0 0 0 rgba(241, 241, 241, 0);
  }
  1% {
    transform: scale(0.96) translateY(10px);
    opacity: 0;
    box-shadow: 0 0 0 rgba(241, 241, 241, 0);
  }
  100% {
    transform: scale(1) translateY(0px);
    opacity: 1;
    box-shadow: 0 0 500px rgba(241, 241, 241, 0);
  }
}
.popup-content {
  -webkit-animation: anvil 0.3s cubic-bezier(0.38, 0.1, 0.36, 0.9) forwards;
}
.popup-overlay {
  background: #00000078;
}
.modal {
  background: #262626;
  color: white;
  width: 400px;
  font-size: 20px;
  padding: 20px;
  border-radius: 10px;
  box-shadow: -1px -3px 15px 3px rgb(0 0 0 / 78%);
}
.modal > .header {
  width: 100%;
  border-bottom: 1px solid gray;
  font-size: 28px;
  color: #f8b284;
  text-align: center;
  padding: 5px;
}
.modal > .content {
  width: 100%;
  padding: 10px 5px;
}
.modal > .actions {
  width: 100%;
  padding: 10px 5px;
  margin: auto;
  text-align: center;
}
.modal > .close {
  cursor: pointer;
  position: absolute;
  display: block;
  padding: 2px 5px;
  line-height: 20px;
  right: -10px;
  top: -10px;
  font-size: 24px;
  background: #ffffff;
  border-radius: 18px;
  border: 1px solid #cfcece;
}
button:hover{
  cursor: pointer;
}
.main_container{
  display: inline-flex;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.background_image{
  background : url("images/background.jpg");
}

.col-6{
  position: relative;
  display: block;
  width: 50%;
  height: 100%;
}
.big_title{
  color: white;
  font-size: 95px;
  text-shadow: 3px 3px 17px rgb(0 0 0 / 89%);
  width: 79%;
  margin: 0 auto;
  text-align: left;
  line-height: 1;
  position: absolute;
  top: 50%;
  left: 40%;
  margin-top: -180px;
  margin-left: -200px;
}
.float_right{
  float:right;  
}
.connect_button{
  position: absolute;
  top: 20px;
  right: 36px;
  z-index: 1000;
  font-size: 18px;
  padding: 9px 18px;
  background: #212121;
  color: white;
  border: none;
  border-radius: 6px;
  box-shadow: 1px 1px 7px 1px rgb(0 0 0 / 63%);
}
.connect_button span{
  text-overflow: ellipsis;
  width: 152px;
  display: inline-block;
  overflow: hidden;
  white-space: nowrap;
}
.connect_button img{
  height: 20px;
  margin-left: 10px;
  margin-bottom: -4px;
}
.mint_container{
  color: white;
  width: 401px;
  background: #262626;
  position: absolute;
  top: 45%;
  left: 30%;
  margin-top: -250px;
  margin-left: -200px;
  border-radius: 10px;
  padding: 55px 8px;
  box-shadow: -1px -3px 15px -7px rgb(0 0 0 / 63%);
}

.divider{
  margin: 20px auto;
  height: 1px;
  width: 80%;
  background-color: #545454;
}
.grid_container{
  margin: 0px 20px;
  display: flex;
}
.grid_td{
  text-align: left;
  padding: 6px 17px;
  flex-grow: 1;
}
.grid_title{
  color: #f8b284;
  font-weight: 300;
}
.flex_even_distance{
  justify-content: space-between;
}
.grid_td p{
  margin: 0;
  font-size: 28px;
}
.grid_td button{
  width: 30px;
  height: 30px;
  background: #ea9e56;
  font-size: 22px;
  line-height: 0;
  border: none;
  padding: 0;
  color: white;
  border-radius: 5px;
}
.flex_grow_0{
  flex-grow: 0 !important;
}
.mint_button{
  width: 100% !important;
  display: inline;
}